.header {
  font-size: 25px;
  font-family: Orbitron;
  text-align: left;
  margin: 5px;
  cursor: pointer;
}
.homeHeader {
  border-bottom: 1px solid black;
}
.mainHeader {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  background-color: #d9d9d9;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin: 5px;
  padding: 10px;
}
.mainHeaderRightSide {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}
.mainHeaderLeftSide {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
}
.icon {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  cursor: pointer;
}
.sideIconMenuHide {
  display: none;
}
.sideIconMenuShow {
  display: block;
}
.txtBtn {
  display: none;
}
@media (min-width: 767px) {      
  .sideIconMenuShow {
    display: none;
  }
  .txtBtn {
    display: block;
  }        
}


