  display: inline-flex;                
}
.cartItemPrice {
  color: black;
  width: 50%;                     
}
.cartItemDelete {
  border: none;
  background: none;
  text-align: right;                
  width: 50%;        
}     
#idIconDelete {
  cursor: pointer;                
}      
