    margin-bottom: 15px;
  }
}
@media (min-width: 768px) {
  .cartItemImage {
    height: 190px;
  }
}
@media (max-width : 768px) {
  .cartItemInfo {
    -ms-flex-line-pack: center;
        align-content: center;
  }
  .cartItemTitle {
    width: 100%;
  }
  .cartItemInfoWraperEnd {
    width: 100%;
    margin-left: 15px;                  
    margin-right: 15px;                  
    margin-top: 15px;                  
  }
}


